import React, { useEffect, useState } from "react";
import Base from "../Base";
import Aboutme from "../shared-components/Aboutme";
import AsProfessional from "../shared-components/AsProfessional";
//import Footer from "../shared-components/Footer";
import HomeBanner from "../shared-components/HomeBanner";
import HomeMiddleBanner from "../shared-components/HomeMiddleBanner";
import MySkills from "../shared-components/MySkills";
import ServiceCategories from "../shared-components/ServiceCategories";
import { useDispatch, useSelector } from "react-redux";
import { getHomeData } from "../reducers/homeReducer";
import axios from "axios";

const Home = () => {
  const dispatch = useDispatch();
  const { error, loading, success, userdetails } = useSelector(
    (state) => state.user
  );

  const [homeData, setHomeData] = useState({});
  const [userProfile, setUserProfile] = useState({});

  const getHomeScreenData = async () => {
    const data = await dispatch(
      getHomeData({ token: userdetails.token ? userdetails.token : null })
    );
    if (!data.error) {
      await setHomeData(data.payload);
    } else {
      await setHomeData({});
    }
  };

  useEffect(() => {
    const getUserData = async () => {
      const { data } = userdetails.token
        ? await axios.get(
            `http://localhost:8081/api/user/info/${userdetails.token}`
          )
        : await axios.get(`http://localhost:8081/api/user/info`);
      if (!data.error) {
        await setUserProfile(data);
      } else {
        await setUserProfile({});
      }
    };
    getUserData();
    getHomeScreenData();
  }, []);
  return (
    <>
      <Base>
        <div className="container-fluid px-0">
          {homeData.banner && homeData.banner.active ? (
            <HomeBanner banner={homeData.banner} userProfile={userProfile} />
          ) : (
            ""
          )}
          <Aboutme aboutme={homeData.pageContent1} />
          <ServiceCategories services={homeData.services} />
          <HomeMiddleBanner middleBanner={homeData.middleBanner} />
          <AsProfessional content={homeData.pageContent2} />
          <MySkills skills={homeData.skills} />
        </div>
      </Base>
    </>
  );
};

export default Home;
