import React from 'react'
import { Container } from 'reactstrap'

const HomeMiddleBanner = () => {
  return (
      <>
          <Container fluid className='m-0 p-0'>
              <div className='home-middle-banner position-relative my-5'>
                  <img src='/images/home-banner.jpg' alt="I am delivering beautiful digital products for my clients" className='w-100' />
              </div>
          </Container>
      </>
  )
}

export default HomeMiddleBanner