import React from "react";
import { Container, Button } from "reactstrap";
import parse from "html-react-parser";

const Aboutme = ({ aboutme }) => {
  const handleDownload = (filename) => {
    // Replace 'https://example.com/sample.pdf' with the URL of the file you want to download
    const fileUrl = `/uploadedFiles/${filename}`;
    const link = document.createElement("a");
    link.href = fileUrl;
    link.download = filename; // Specify the desired file name
    link.click();
  };
  return (
    <>
      {aboutme && (
        <Container className="text-center ">
          <h1 className="p-color f-size-20 py-4 mt-5">
            {parse(`${aboutme.contentTitle}`)}
            <hr className="w-200px my-4 mx-auto" />
          </h1>
          <h3 className="t-color f-size-36 py-3">
            {/* Creative Frontend Developer based in India */}
            {parse(`${aboutme.contentHeading}`)}
          </h3>
          <div className="t-color f-size-20 pb-5">
            {parse(`${aboutme.contentData}`)}
          </div>
          <div className="py-3 mb-5">
            <Button
              className="sp-btn-secondary rounded-0 px-5"
              onClick={() => handleDownload(aboutme.attachment)}
            >
              DOWNLOAD MY RESUME
            </Button>
          </div>
        </Container>
      )}
    </>
  );
};

export default Aboutme;
