import React from 'react'
import { Container } from 'reactstrap'

const AsProfessional = () => {
  return (
      <>
      <Container className='text-center '>
              <h1 className='p-color f-size-20 py-4 mt-5'>
                  AS A PROFESSIONAL
                  <hr className='w-200px my-4 mx-auto'/>
              </h1>
              <h3 className='t-color f-size-36 py-3'>I have high skills in developing and programming</h3>
              <p className='t-color f-size-20 pb-5'>I am working on a professional, visually sophisticated and technologically<br/>proficient, responsive and multi-functional personal.
              </p>
          </Container>
      </>
  )
}

export default AsProfessional